import { mapState, mapActions } from 'vuex'
import { queryString } from '../../../../../utils/getQueryString'
import FeedbackChat from '../../../../../components/feedbackChatTemplate'

export default {
  data () {
    return {
      id: 0,
      inputVal: ''
    }
  },
  onLoad (options) {
    if (options.id) {
      this.id = options.id
    } else {
      let scene = decodeURIComponent(options.scene)
      this.id = queryString(scene, 'id')
    }
  },
  onReady () {
    this.getFeedbackDetailsFn()
  },
  computed: {
    ...mapState([
      'userInfo',
      'feedbackDetails'
    ])
  },
  methods: {
    ...mapActions([
      'getFeedbackDetails',
      'addFeedbackReply'
    ]),
    getQueryString (url, name) {
      let reg = new RegExp('(^|&|/?)' + name + '=([^&|/?]*)(&|/?|$)', 'i')
      let r = url.substr(1).match(reg)
      if (r !== null) {
        return r[2]
      }
      return null
    },
    // 获取反馈详情
    getFeedbackDetailsFn () {
      const data = {
        that: this,
        id: this.id
      }
      this.getFeedbackDetails(data)
    },
    // 提交
    onSubmitFn () {
      const data = {
        that: this,
        inputVal: this.inputVal,
        id: this.id
      }
      this.addFeedbackReply(data).then((res) => {
        this.showToastFn(res.message)
        if (res.ret === 1) {
          this.feedbackDetails.reply.push({ content: this.inputVal, is_user: 1 })
          this.inputVal = ''
        }
      })
    },
    // 预览图片
    onPreviewImageFn (arr, url) {
      uni.previewImage({
        current: url,
        urls: arr
      })
    },
    showToastFn (title) {
      uni.showToast({
        title,
        icon: 'none',
        duration: 2000
      })
    }
  },
  onUnload () {
    this.inputVal = ''
  },
  components: {
    FeedbackChat
  }
}
